<template>
  <div class="search_address_page">
    <section>
      <head-top head-title="搜索" go-back='true'></head-top>
      <form class="search_form">
        <input type="search" name="search" placeholder="请输入小区/写字楼/学校等" v-model="searchValue">
        <button @click.prevent="searchPlace()">搜索</button>
      </form>
      <ul class="address_list" v-if="searchData">
        <li v-for="(item, index) in searchData" :key="index" @click="choooedAddress(item)">
          <h4>{{item.name}}</h4>
          <p>{{item.address}}</p>
        </li>
      </ul>
      <div v-else class="empty_tips">
        <p>找不到地址？</p>
        <p>尝试输入小区、写字楼或学校名</p>
        <p>详细地址（如门牌号等）可稍后输入哦</p>
      </div>
    </section>
  </div>
</template>

<script>
  import headTop from '@/components/header/head'
  import {searchNearby} from '@/service/getData'
  import {mapMutations} from 'vuex'

  export default {
    data(){
      return{
        searchValue: null, //输入的搜索内容
        searchData: null, //搜索的结果
      }
    },
    components: {
      headTop,
    },
    props:[],
    methods: {
      ...mapMutations([
        'CHOOSE_SEARCH_ADDRESS'
      ]),
      //搜索
      async searchPlace(){
        if (this.searchValue) {
          this.searchData = await searchNearby(this.searchValue);
        }
      },
      //选择搜素结果
      choooedAddress(item){
        this.CHOOSE_SEARCH_ADDRESS(item);
        this.$router.go(-1);
      },

    }
  }
</script>

<style lang="scss" scoped>
  @import '@/../static/style/mixin';

  .search_address_page{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    z-index: 204;
    overflow-y: auto;
    padding-top: 46px;
    p, span{
      font-family: Helvetica Neue,Tahoma,Arial;
    }
  }
  .search_form{
    display: flex;
    padding: 17px;
    input{
      @include sc(15px, #999);
      flex: 4;
      background-color: #f1f1f1;
      margin-right: 14px;
      height: 36px;
      border-radius: 3px;
      padding: 0 10px;
    }
    button{
      flex: 1;
      @include sc(16px, #fff);
      background-color: $blue;
      border-radius: 3px;
    }
  }
  .address_list{
    padding: 17px;
    li{
      padding: 17px 0;
      border-bottom: 1px solid #f5f5f5;
      line-height: 24px;
      h4{
        @include sc(17px, #555);
      }
      p{
        @include sc(15px, #999);
      }
    }
  }
  .empty_tips{
    @include center;
    width: 100%;
    p{
      @include sc(12px, #aaa);
      line-height: 17px;
      text-align: center;

    }
  }
</style>



